/*
##Helpers
  Helper classes for the UI

  Responsive embeds
```
<div class="embed-responsive">
  <iframe></iframe>
</div>
```

__margin__
*top
*bottom
*y
*x
*none

    <div class="margin-{value}"></div>
*/
// Responsive embeds
.#{$utility-prefix}embed-responsive {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
  max-width: 100%;
  embed,
  iframe,
  object {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}
// Positions
.#{$utility-prefix}absolute--center {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  display: flex;
  align-items: center;
  > * {
    margin: 0 auto;
  }
}
.#{$utility-prefix}static {
  position: static;
}
.#{$utility-prefix}absolute {
  position: absolute;
}
.#{$utility-prefix}relative {
  position: relative;
}
// Overflow
.#{$utility-prefix}overflow-auto {
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;
}
.#{$utility-prefix}overflow-hidden {
  overflow: hidden;
}
.#{$utility-prefix}p0 {
  padding: 0;
}
